@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@500&display=swap");
.Izohlar{
    margin-top: 120px;
}
.Izohlar .row .col-12 h2{
    margin-left: 15px;
    font-family: Montserrat;
    font-style: normal;
    font-weight: bold;
    font-size: 33px;
    line-height: 40px;
    letter-spacing: 0.02em;
    text-transform: capitalize;
    margin-bottom: 20px;

color: #00445B;
}
.IzohCard{
    background: #ECFEFA;
    box-shadow: 0px 5px 15px rgba(0, 0, 0, 0.13);
    padding-bottom: 10px;
}
.IzohCard .IzohCard-body{
    padding: 0 20px;
    line-height: 30px;
}
.IzohCard .IzohCard-body img{
    width:100px;
    height: 18px;
}