nav{
    width: 100%;
    height: 100px;
    display: flex;
    align-items: center;
    justify-content:space-between;
    padding: 0 5%;
    padding-top: 1%;
    .brand{
        width: 287px;
        height: 100px;
        a{
            width: 100%;
            height: 100px;
            img{
                width: 100%;
                height: 100px;
            }
        }
    }
    ul{
        margin-bottom: 0;
        list-style: none;
        display:flex;
        align-items: center;
        justify-content: flex-end;
        width: calc(100% - 287px);
        li{
            margin-left: 4%;
            a{
                text-decoration: none;
                color: black;
                font-size: 18px;
            }
        }
        .fa-times{
            display: none;
        }
    }
    .fa-bars{
        display: none;
    }
}

