nav {
  width: 100%;
  height: 100px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  padding: 0 5%;
  padding-top: 1%;
}

nav .brand {
  width: 287px;
  height: 100px;
}

nav .brand a {
  width: 100%;
  height: 100px;
}

nav .brand a img {
  width: 100%;
  height: 100px;
}

nav ul {
  margin-bottom: 0;
  list-style: none;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: end;
      -ms-flex-pack: end;
          justify-content: flex-end;
  width: calc(100% - 287px);
}

nav ul li {
  margin-left: 4%;
}

nav ul li a {
  text-decoration: none;
  color: black;
  font-size: 18px;
}

nav ul .fa-times {
  display: none;
}

nav .fa-bars {
  display: none;
}

@media (max-width: 1200px) {
  nav {
    position: relative;
  }
  nav ul {
    display: block;
    background-color: aqua;
    position: absolute;
    top: 0;
    right: 0;
    width: 0px;
    opacity: 0;
    -webkit-transition: 0.3s linear;
    transition: 0.3s linear;
  }
  nav ul li {
    margin: 5px 0;
  }
  nav ul .fa-times {
    display: block;
    position: absolute;
    top: 15px;
    left: 15px;
  }
  nav .fa-bars {
    display: block;
  }
}

.active {
  display: block;
  opacity: 1;
  width: 150px;
  -webkit-transition: 0.3s linear;
  transition: 0.3s linear;
}
