@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@500&display=swap");

.Kurslar{
    margin-top: 100px;
}

.Kurslar .row .col-12 h2{
    margin-left: 15px;
    font-family: Montserrat;
    font-style: normal;
    font-weight: bold;
    font-size: 33px;
    line-height: 40px;
    letter-spacing: 0.02em;
    text-transform: capitalize;
    margin-bottom: 20px;

color: #00445B;
}
.Kurslar .row .col-12 .KursCard{
  width: 100%;

  background-color: rgb(236,254,250);
  padding-bottom: 15px;
}
.Kurslar .row .col-12 .KursCard .KursCard-header{
    width: 100%;
   
}
.Kurslar .row .col-12 .KursCard .KursCard-header img{
    width: 100%;
}
.Kurslar .row .col-12 .KursCard .KursCard-body,
.Kurslar .row .col-12 .KursCard .KursCard-footer{
    padding: 0 30px;
    margin-top: 15px;
  
}
.Kurslar .row .col-12 .KursCard .KursCard-body h3{
     font-family: Montserrat ;
     margin-bottom: 10px;
}
.Kurslar .row .col-12 .KursCard .KursCard-body p{

}
.Kurslar .row .col-12 .KursCard .KursCard-footer a{
    width: 100%;
    height: 46px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #00445B;
    font-family: Montserrat;
    font-style: normal;
    font-size: 14px;
    line-height: 17px;
    letter-spacing: 0.06em;
    text-decoration: none;
    color: #FFFFFF;
}