@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@500&display=swap');
.HomeCop{
    .row{
        align-items: center;
        .HomeCop-left{
            h1{
                font-family: Montserrat;
                font-style: normal;
                font-weight: bold;
                font-size: 50px;
                letter-spacing: 0.02em;
            }
            p{
                font-family: Montserrat;
                font-style: normal;
                font-weight: 500;
                margin: 20PX 0;
                font-size: 23.4946px;
                letter-spacing: 0.02em;
            }
            a{
                display:flex;
                align-items: center;
                justify-content: center;
                width: 234px;
                height: 65px;
                font-family: Montserrat;
                padding: 0 25px;
                font-weight: bold;
                font-size: 14px;
                letter-spacing: 0.06em;
                font-style: normal;
                line-height: 17px;
                color: #FFFFFF;
                background: #00445B;
                text-decoration: none;
            }
        }
        .HomeCop-right{
                display:flex;
                align-items: center;
                justify-content: flex-end;
                position: relative;
            img{
                width: 407px;
                height: 578px;
                transform: matrix(1, 0, 0, 1, 0, 0);            }
            div{
                position: absolute;
                bottom: -60px;
                right: 20px;
                background: #C7EAFF;
                filter: blur(20px);
                width: 300px;
                height: 135px;
            }
        }
    }
}