@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@500&display=swap");
.Natijalar{
    margin-top: 120px;
}

.NatijaCard{
    display: block;
    width: 80%;
    padding: 0 15px;
    margin: 0 auto;
    background: rgb(236,254,250);
    box-shadow: 0px 3.55979px 10.6794px rgba(0, 0, 0, 0.13);
    margin-top: 50px;
}
.Natijalar   .row .col-12 h2{
    /*margin-left: 15px;*/
    font-family: Montserrat;
    font-style: normal;
    font-weight: bold;
    font-size: 33px;
    line-height: 40px;
    letter-spacing: 0.02em;
    text-transform: capitalize;
    margin-bottom: 20px;
    color: #00445B;
}
.NatijaCard .NatijaCard-header img{
    width: 30%;
    display: block;
    margin: 0 auto;
    transform: translateY(-50%);
}
.slick-slide{
    padding: 0 15px;
}
.NatijaCard{
    /* background: #eee5e5; */
    padding-bottom: 15px;
    box-shadow: 0px 3.55979px 10.6794px rgba(0, 0, 0, 0.13);
}
.NatijaCard .NatijaCard-body p{
    font-family: Montserrat;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 30px;
    letter-spacing: 0.02em;
    color: #000000;
}
.NatijaCard .NatijaCard-body h4{
    font-family: Montserrat;
    font-style: normal;
    font-weight: bold;
    font-size: 18px;
    line-height: 40px;
    letter-spacing: 0.02em;
    color: #000000;
}
