@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@500&display=swap");


.Aloqa{
    margin-top: 120px;
    margin-bottom: 120px;
}

.Aloqa-header{
    margin-bottom: 30px;
}
.Aloqa-header h2{
font-family: Montserrat;
font-style: normal;
font-weight: bold;
font-size: 33px;
line-height: 40px;
letter-spacing: 0.02em;
text-transform: capitalize;  /*Bosh harifni kattalashtirib beradi har doim*/
color: #00445B;
}
.Aloqa-body img{
width: 100%;
border-radius: 10px;
}
.Aloqa-footer{
    margin-top:20px;
}
.Aloqa-footer .Aloqa-footer-left , .Aloqa-footer-right {
 display: flex;
 align-items: center;
}
.Aloqa-footer .Aloqa-footer-left a , .Aloqa-footer-right a{
    text-decoration: none;
    font-family: Montserrat;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 120%;
    margin: 0 15px;
    letter-spacing: 0.02em;
    color: #000000;
}
.Aloqa-footer .Aloqa-footer-left svg , .Aloqa-footer-right svg{
font-size: 28px;
color: #00445B;
}
.Aloqa-footer .Aloqa-footer-right svg{
    margin-left: 15px;
}